.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  padding: 15px;

  background-color: $primary-black-alpha;
}

.modal {
  position: absolute;
  margin: 0 auto;
  padding: 15px;
  z-index: 4;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 290px;
  max-width: 450px;

  border-radius: 15px;
  background-color: $primary-white;
}

.modal__title {
  font-size: 20px;
  line-height: 1.5;
  font-weight: 700;
  text-align: center;
  margin-bottom: 12px;
}

.modal__form,
.modal__textarea {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  & label {
    margin-bottom: 4px;
  }
}

.modal__input {
  height: 40px;
  margin-bottom: 10px;

  font-size: 18px;

  border: 1px solid;
  border-radius: 4px;
  color: $primary-black;
}

.modal__textarea {
  & textarea {
    resize: none;
    width: 100%;

    height: 80px;
    margin-bottom: 10px;

    font-size: 18px;

    border: 1px solid;
    border-radius: 4px;
    color: $primary-black;
  }
}

.modal__btns {
  display: flex;
  gap: 10px;
  flex-grow: 1;
}

.modal__btn {
  height: 100%;

  color: $primary-black;
  border-color: $primary-black;

  &:hover,
  &:focus {
    background-color: $primary-black-alpha;
  }
}
